@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700;900&display=swap);
.homepage__title{font-size:40px;margin:var(--gutter) 0}.homepage__title h1{margin:0}.homepage__banner{margin-top:var(--gutter)}.homepage__banner img{height:300px}.homepage__image-credit{font-size:12px;margin-top:8px}.homepage__image-credit a{color:var(--font-secondary2)}

.footer{padding:40px 0;border-top:var(--border-default);background:#f2f2f2}.footer__middle{flex:1 1}.footer__inner{width:100%;box-sizing:border-box;padding:0 var(--gutter);max-width:var(--restrict-width);margin:0 auto;display:flex;align-items:center}.footer__socials{display:flex;margin:0 auto;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.footer__socials div:not(:last-child){margin-right:8px}.footer__logo{font-size:44px;font-weight:900}.footer__navigation-title{text-transform:uppercase;font-size:14px;color:var(--font-secondary);font-weight:600;margin-bottom:4px}.footer__navigation-links{font-size:14px}

.header{display:flex;align-items:center;height:80px;box-shadow:var(--box-shadow-bottom);position:-webkit-sticky;position:sticky;top:0;z-index:9999;background:#fff}.header__inner{width:100%;box-sizing:border-box;padding:0 var(--gutter);max-width:var(--restrict-width);padding:0 var(--gutter);margin:0 auto;display:flex;align-items:center}.header__left{flex:1 1;margin-right:20px;display:flex;align-items:center}.header__logo{display:block;font-size:44px;font-weight:900;margin-right:16px}.header__right{height:24px}.header__profile-icon{cursor:pointer}.header__profile-icon svg{width:24px;height:24px}

.layout{min-height:100vh;display:flex;flex-direction:column}.layout__main{box-sizing:border-box;padding:var(--gutter);flex:1 1;max-width:var(--restrict-width);margin:0 auto;width:100%}

.button{border:none;margin:0;padding:0;width:auto;overflow:visible;color:inherit;font:inherit;line-height:normal;-webkit-font-smoothing:inherit;-moz-osx-font-smoothing:inherit;-webkit-appearance:none;background:#3cc0e8;color:#fff;padding:10px 20px;border-radius:4px;transition:all 0.5s ease;cursor:pointer;border:1px solid transparent}.button:hover{background:#fff;color:var(--font);border:1px solid #f0f0f0}.button--disabled{cursor:not-allowed;background:#c2c2c2 !important;color:#eee !important}.button--loading{background:#dfdfdf !important;color:#313131 !important}

.text-field input{width:100%;padding:10px 14px;margin-bottom:8px;display:inline-block;border:1px solid #ccc;border-radius:4px;box-sizing:border-box}.text-field__label{margin-bottom:4px}

.products-table table{font-family:arial, sans-serif;border-collapse:collapse;width:100%}.products-table td,.products-table th{border:1px solid #dddddd;text-align:left;padding:8px}.products-table tr:nth-child(even){background-color:#f0f0f0}.products-table__product-image img{display:block;margin:0 auto;width:24px;height:24px}.products-table__product-actions{display:flex;align-items:center}.products-table__product-actions div{cursor:pointer}

.products-page__actions{display:flex;justify-content:space-between;margin-bottom:8px;align-items:center}.products-page__refresh{cursor:pointer}.products-page__refresh svg{width:24px;height:24px;border:1px solid #d3d3d3;padding:3px}

.product-form__submit{margin-top:var(--gutter);display:flex;justify-content:flex-end}

.add-product__title{font-size:14px;text-transform:uppercase;color:var(--font-secondary2);margin-bottom:24px}

.edit-product__title{font-size:14px;text-transform:uppercase;color:var(--font-secondary2);margin-bottom:24px}

:root{--primary: #000;--secondary: #fff;--box-shadow-bottom: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.1);--font: rgba(0, 0, 0, 0.78);--font-size: 16px;--font-secondary: #a7a7a7;--font-secondary2: rgb(114, 128, 143);--gray-background: #f5f8fc;--border-default: 1px solid #e4e6ea99;--restrict-width: 1400px;--indigo: #6610f2;--purple: #6f42c1;--pink: #e83e8c;--red: #dc3545;--yellow: #ffc107;--green: #28a745;--teal: #20c997;--cyan: #17a2b8;--white: #fff;--gray: #6c757d;--gray-dark: #343a40;--success: #28a745;--info: #17a2b8;--warning: #ffc107;--danger: #ff5245;--light: #f5f8fc;--dark: #343a40;--cold: #216dfb;--hot: #e85d60;--gutter: 25px;--padding: 50px;--font-family-default: 'Roboto', sans-serif}html,body{margin:0;padding:0;font-size:16px;font-size:var(--font-size);color:rgba(0, 0, 0, 0.78);color:var(--font);font-family:'Roboto', sans-serif;font-family:var(--font-family-default)}a{color:rgba(0, 0, 0, 0.78);color:var(--font);text-decoration:none;font-weight:500}a:hover{color:#17a2b8;color:var(--info)}

